import React, { Component } from "react";
import Form from "react-validation/build/form";
import Select from "react-validation/build/select";
import Textarea from "react-validation/build/textarea";
import CheckButton from "react-validation/build/button";
import BackendService from "../../services/backend.service"
import _t from "../../services/i18n.service";

const required = value => {
    if (!value || value.length === 0) {
      return (
        <div className="alert alert-danger" role="alert">
          {_t('field_required')}
        </div>
      );
    }
  };

class NewQuestion extends Component {

    constructor(props) {
        super(props);

        this.handleCreate = this.handleCreate.bind(this);
        this.onSelectQuestion = this.onSelectQuestion.bind(this);
        this.onSetPhase = this.onSetPhase.bind(this);
        this.onSetPrePost = this.onSetPrePost.bind(this);
        this.onSetPrompt = this.onSetPrompt.bind(this);

        this.state = {
            title: '',
            description: '',
            phase: 0,
            prepost: '',
            prompt: 'none',
            notification_options: ["Once per week"],        

            successful: false,
            message: '',
        }
    }

    async componentDidMount() {
        const config = await BackendService.getServerConfig();
        if (config) {
            let notification_options = config.exercise_notification_options;
            notification_options = Array.isArray(notification_options) ? notification_options : [notification_options];
            this.setState({notification_options: notification_options});
        }
    }

    onSelectQuestion(e) {
        this.setState({
          title: e.target.value
        });
      }

    onSetPhase(e) {
        this.setState({
            phase:  parseInt(e.target.value)
        });
    }

    onChangeDescription(e) {
        this.setState({
            description:  e.target.value
        });
    }

    onSetPrePost(e) {
        this.setState({
            prepost: e.target.value
        });
    }

    onSetPrompt(e) {
        this.setState({
            prompt: e.target.value
        });
    }

    handleCreate(e) {

        e.preventDefault();
        this.setState({
          message: "",
          successful: false
        });
        this.form.validateAll();    

        if (this.checkBtn.context._errors.length === 0) {
            BackendService.newQuestion({
                title: this.state.title,
                description: this.state.description,
                phase: this.state.phase,
                prepost: this.state.prepost,
                //notification: this.state.prompt,
                deadline: this.state.deadline
            })
            .then(
                response => {
                    this.setState({
                        message: response.message,
                        successful: true
                    });
                    setTimeout(() => this.props.onNewQuestion(response.question), 1000);
                },
                error => {
                    const resMessage =
                        (error.response &&
                        error.response.data &&
                        error.response.data.message) ||
                        error.message ||
                        error.toString();
                    this.setState({
                        successful: false,
                        message: resMessage
                    });
                }
            );
        }
    }

    render() {
        const question_ids = [...Array(3).keys()];

        return (
            <div>
                <div className="center-form">
                    <div className="">
                        <h5>{_t('new_question_title')}</h5>
                        <Form
                            onSubmit={this.handleCreate}
                            ref={c => {
                            this.form = c;
                            }}
                        >
                            {!this.state.successful && (
                            <div>
                                <div className="form-group mt-2">
                                    <div className="mb-3">
                                        <label className="form-label">{_t('choose_question')}:</label>
                                        <div className="form-check">                                
                                            {question_ids.map((l, i) => 
                                                <div  className="p-1" key={i}>
                                                <input
                                                    key={i}
                                                    type="radio"
                                                    id={'question_'+l}
                                                    name="questionType"
                                                    className="form-check-input"
                                                    value={l+1}
                                                    onChange={this.onSelectQuestion}
                                                    validations={[required]}
                                                /> 
                                                <label className="form-check-label"> {_t('question_type_'+`${l+1}`)}</label>
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                </div>
                                
                                <div className="form-group mt-2 mb-3">
                                    <label htmlFor="phase 1" className="form-check-label">{_t('description')} I</label>
                                    <Textarea
                                        placeholder={_t('description')}
                                        value=""
                                        name="description"
                                        validations={[required]}
                                    />                                      
                                </div>

                                <div className="form-group mt-2 mb-3">
                                    <div className="form-check">
                                        <input
                                            type="radio"
                                            id="phase 1"
                                            name="phase"
                                            value="1"
                                            className="form-check-input"
                                            onChange={this.onSetPhase}
                                            defaultChecked
                                        />
                                        <label htmlFor="phase 1" className="form-check-label">{_t('phase')} I</label>
                                    </div>
                                    <div className="form-check">
                                        <input
                                            type="radio"
                                            id="phase 2"
                                            name="phase"
                                            value="2"
                                            className="form-check-input"
                                            onChange={this.onSetPhase}
                                        /> 
                                        <label htmlFor="phase 2" className="form-check-label">{_t('phase')} II</label>
                                    </div>
                                </div>
                                <div className="form-group mt-2 mb-3">
                                    <div className="form-check">
                                        <input
                                            type="radio"
                                            id="pre"
                                            name="prepost"
                                            value="pre"
                                            className="form-check-input"
                                            defaultChecked
                                            onChange={this.onSetPrePost}
                                        />             
                                        <label htmlFor="pre" className="form-check-label">Pre</label>                            
                                    </div>
                                    <div className="form-check">
                                        <input
                                            type="radio"
                                            id="post"
                                            name="prepost"
                                            value="post"
                                            className="form-check-input"
                                            onChange={this.onSetPrePost}
                                        /> 
                                        <label htmlFor="post" className="form-check-label">Post</label>
                                    </div>
                                </div>

                                <div className="form-group mt-2">
                                    <label className="form-label">{_t('reminder')}:</label>
                                    <div className="col-12 text-center">
                                        <Select name='prompt' value='none' className='form-select' validations={[required]}>
                                            <option value='none'>{_t('none')}</option>
                                            {this.state.notification_options.map((e, i) => (
                                                <option key={i} value={e}>{_t(e)}</option>
                                            ))}
                                        </Select>
                                    </div>
                                </div>                                

                                <div className="form-group mt-5 row">
                                    <div className="col-6 text-center">
                                        <button className="btn btn-secondary btn-block" onClick={this.props.onCancelNewQuestion}>{_t('cancel')}</button>
                                    </div>
                                    <div className="col-6 text-center">
                                        <button className="btn btn-success btn-block" onClick={this.handleCreate}>{_t('create')}</button>
                                    </div>
                                </div>
                            </div>
                            )}
                            {this.state.message && (
                            <div className="form-group">
                                <div
                                className={
                                    this.state.successful
                                    ? "alert alert-success"
                                    : "alert alert-danger"
                                }
                                role="alert"
                                >
                                {this.state.message}
                                </div>
                            </div>
                            )}
                            <CheckButton
                                style={{ display: "none" }}
                                ref={c => {
                                this.checkBtn = c;
                                }}
                            />
                        </Form>
                    </div>
                </div>
            </div>
        );    
    }
} 

export default NewQuestion;