import React, { Component } from "react";
import _t from "../../services/i18n.service";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import BackendService from "../../services/backend.service";
import Classroom from "./classroom.component.js";
import Loading from "../loading.component";
import { Message, showMsg } from "../message.component";
import authService from "../../services/auth.service";

class Student extends Component {
  constructor(props) {
    super(props);

    this.onChangeClassroom = this.onChangeClassroom.bind(this);
    this.onSelectClassroom = this.onSelectClassroom.bind(this);
    this.onJoinClassroom = this.onJoinClassroom.bind(this);
    this.setMsg = this.setMsg.bind(this);

    this.state = {
      classrooms: [],
      classroom_new_id: "",
      classroom_id: "",

      show: "loading",

      successful: false,
      message: {show: false}
    };
  }

  setMsg(message) {
    this.setState({message: message});
  }

  componentDidMount() {
    this.setState({ show: "loading" });
    BackendService.getStudentClassrooms()
      .then((res) => {
        this.setState({ classrooms: res, show: "default" });
      })
      .catch(() => {
        this.state.show = "default";
        showMsg(_t("unable_to_get_data"), "danger", this.setMsg);
      });
  }

  onChangeClassroom(e) {
    this.setState({
      classroom_new_id: e.target.value.toUpperCase(),
      classroom_id: "",
    });
  }

  onSelectClassroom(e) {
    this.setState({
      classroom_id: e.target.value,
    });
  }

  onJoinClassroom(e) {
    const code =
      this.state.classroom_new_id.length > 0
        ? this.state.classroom_new_id
        : this.state.classroom_id;

    // check if already subscribed
    if (!this.state.classrooms.find(e => e.code === code)) {
        const user = authService.getCurrentUser();
        // not subscribe, so let's subscribe!
        BackendService.subscribeToClassroom(code, user.email)
        .then(res => BackendService.getStudentClassroom(code,))
        .then(classroom => this.setState({classroom: classroom, show: 'classroom'}))
        .catch(err => showMsg(_t("operation_failed"), "danger", this.setMsg));
    }
  }

  render() {
    const classrooms = this.state.classrooms;
    return (
      <>
        {this.state.message.show && (
          <Message text={this.state.message.text} type={this.state.message.type} />
        )}
        {this.state.show === "loading" && <Loading />}
        {this.state.show === "classroom" && (
          <Classroom classroom={this.state.classroom} goBack={() => this.componentDidMount()}/>
        )}
        {this.state.show === "default" && (
          <div>
            <Form
              onSubmit={this.handleRegister}
              ref={(c) => {
                this.form = c;
              }}
            >
              {!this.state.successful && (
                <div>
                  <div className="form-group mt-2">
                    <Input
                      type="text"
                      className="form-control"
                      placeholder={_t("new_classroom_id")}
                      name="classroom_new_id"
                      value={this.state.classroom_new_id}
                      onChange={this.onChangeClassroom}
                      maxLength="4"
                      validations={[]}
                    />
                  </div>

                  <div className="form-group mt-2 row">
                    <div className="col-12 text-center">
                      <Select
                        name="classroom_id"
                        value={this.state.classroom_id}
                        className="form-select"
                        validations={[]}
                        disabled={this.state.classroom_new_id.length > 0}
                        onChange={this.onSelectClassroom}
                      >
                        <option value="" className="text-muted">
                          {_t("select_previous")}
                        </option>
                        {classrooms.map((c, i) => (
                          <option key={i} value={c.code}>
                            {c.title}
                          </option>
                        ))}
                      </Select>
                    </div>
                  </div>

                  <div className="form-group mt-2 row">
                    <div className="col-12 text-center">
                      <button
                        className="btn btn-success btn-block"
                        onClick={this.onJoinClassroom}
                      >
                        {_t("join")}
                      </button>
                    </div>
                  </div>
                </div>
              )}
              {this.state.message && (
                <div className="form-group">
                  <div
                    className={
                      this.state.successful
                        ? "alert alert-success"
                        : "alert alert-danger"
                    }
                    role="alert"
                  >
                    {this.state.message}
                  </div>
                </div>
              )}
              <CheckButton
                style={{ display: "none" }}
                ref={(c) => {
                  this.checkBtn = c;
                }}
              />
            </Form>
          </div>
        )}
      </>
    );
  }
}

export default Student;
