import React, { Component } from "react";
import {
  HashRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import config from "./config";
//import AuthService from "./services/fake_auth.service";
import AuthService from "./services/auth.service";

import ACL from "./services/acl.service";
import Unauthorized from "./components/unauthorized.component";
import Login from "./components/login.component";
import Register from "./components/register.component";
import Language from "./components/language.component";

import Layout from "./components/layout.component";
import Professor from "./components/professor/professor.component";
import Student from "./components/student/student.component";
import Stats from "./components/student/stats.component";
import DelUser from "./components/student/del_user.component";
import NotFound from "./components/notfound.component";
import ChangePassword from "./components/change_password.component";

class App extends Component {
  constructor(props) {
    super(props);
    this.logOut = this.logOut.bind(this);
    this.logIn = this.logIn.bind(this);
    this.onChangeLang = this.onChangeLang.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.delUser = this.delUser.bind(this);

    this.state = { user: AuthService.getCurrentUser() };

    const lang = window.localStorage.getItem("lang");
    window.$lang = lang ? lang : "en";
  }

  componentDidMount() {
    this.setState({ user: AuthService.getCurrentUser() });
  }

  componentDidUpdate() {
    const lang = window.localStorage.getItem("lang");
    window.$lang = lang ? lang : "en";
  }

  logIn() {
    const user = AuthService.getCurrentUser();
    this.setState({ user: user });
  }

  logOut() {
    AuthService.logout();
    this.setState({ user: AuthService.getCurrentUser() });
    window.location = config.BASE_PATH;
  }

  onChangeLang(lang) {
    window.$lang = lang;
    localStorage.setItem("lang", lang);
    this.forceUpdate();
  }

  delUser(successful) {
    if (successful) {
      this.logOut();
    }
  }

  onChangePassword(successful) {
    if (successful) {
      this.logOut();
    }
  }

  render() {
    const user = this.state.user;

    return (
      <Router>
        <Routes>
          <Route path="/" element={<Layout user={user} logOut={this.logOut} />}>
            <Route
              index
              element={
                user.role && user.role === "ROLE_PROFESSOR" ? (
                  <Navigate to="/prof" replace />
                ) : user.role && user.role === "ROLE_STUDENT" ? (
                  <Navigate to="/stu" replace />
                ) : (
                  <Navigate to="/login" replace />
                )
              }
            />
            <Route
              path="login"
              element={
                <Login cb={this.logIn} onChangeLang={this.onChangeLang} />
              }
            />
            <Route path="unauthorized" element={<Unauthorized />} />
            <Route
              path="register"
              element={
                <Register user={user} onChangeLang={this.onChangeLang} />
              }
            />
            <Route
              path="lang"
              element={<Language onClick={this.onChangeLang} />}
            />

            <Route
              path="prof"
              element={
                <ACL roles={["ROLE_PROFESSOR"]}>
                  <Professor user={user} />
                </ACL>
              }
            />

            <Route
              path="stu"
              element={
                <ACL roles={["ROLE_STUDENT"]}>
                  <Student user={user} />
                </ACL>
              }
            />

            <Route
              path="stu-stats"
              element={
                <ACL roles={["ROLE_STUDENT"]}>
                  <Stats user={user} />
                </ACL>
              }
            />

            <Route
              path="del-user"
              element={
                <ACL roles={["ROLE_STUDENT"]}>
                  <DelUser user={user} delUser={this.delUser} />
                </ACL>
              }
            />

            <Route
              path="chgpass"
              element={
                <ACL roles={["ROLE_STUDENT", "ROLE_PROFESSOR"]}>
                  <ChangePassword
                    user={user}
                    onChangePassword={this.onChangePassword}
                  />
                </ACL>
              }
            />
            <Route path="*" element={<NotFound />} />
          </Route>
        </Routes>
      </Router>
    );
  }
}

export default App;
