import axios from "axios";
import authHeader from "./auth-header";
import config from "../config";
import { professor_data, student_data, classroom_data } from "./fake-content";
import _t from "./i18n.service";

class BackendService {
  bearerConfig() {
    const user = JSON.parse(localStorage.getItem("user"));
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    };
    return config;
  }

  updateJWT(res) {
    if (res && res.data && res.data.jwt) {
      const user = {
        ...JSON.parse(localStorage.getItem("user")),
        jwt: res.data.jwt,
      };
      localStorage.setItem("user", JSON.stringify(user));
    }
  }

  changePassword(email, password, new_password) {
    return new Promise((resolve, reject) => {
      axios
        .post(
          config.API_URL + "/changepassword",
          {
            email: email,
            password: password,
            newPassword: new_password,
          },
          this.bearerConfig()
        )
        .then((res) => {
          this.updateJWT(res);
          resolve({ message: _t("password_changed") });
        })
        .catch((err) => {
          reject({ message: _t("password_change_" + err.response.status) });
        });
    });
  }

  getServerConfig() {
    return new Promise((resolve, reject) => {
      axios
      .get(config.API_URL + "/settings", {}, this.bearerConfig())
      .then(res => resolve(res.data.data[0]))
      .catch(err => reject(err));
    });
  }

  // --------------------------------------------------------------------------
  // PROFESSOR API CALLS
  // --------------------------------------------------------------------------

  async getProfessorClassrooms() {

    return new Promise(async (resolve, reject) => {

      let classrooms = await axios
      .get(config.API_URL + "/classrooms", {}, this.bearerConfig())
      .then((res) => res.data.data);

      if (classrooms.length === 0) {
        return resolve([]);
      }

      // sort inversely by id (so newests are first)
      classrooms = classrooms.sort((a,b) => b.id - a.id);

      // To force all promises to resolve before continuing, 
      // we chain them with map and wait for all to be resolved
      await Promise.all(
        classrooms.map(element => 
          axios
            .get(
              config.API_URL + "/students-count/" + element.code,
              {},
              this.bearerConfig()
            )
            .then((res) => {
              element.num_students = res.data.count;
            })
            .catch(err => {
              element.num_students = 0;
            })
        )
      );

      return resolve(classrooms);
    });
  }

  getProfessorQuestions(code) {
    return new Promise((resolve, reject) => {
      axios
        .get(config.API_URL + "/questions/" + code, this.bearerConfig())
        .then((res) => {
          const questions = res.data.data.sort((a,b) => b.id - a.id); // newests questions first
          resolve(questions);
        })
        .catch((err) => reject(err));
    });
  }

  newClassroom(title) {
    return new Promise((resolve, reject) => {
      axios
        .post(
          config.API_URL + "/classroom",
          { title: title },
          this.bearerConfig()
        )
        .then((res) => {
          resolve(res.data);
        })
        .catch((err) => reject(err));
    });
  }

  delClassroom(code) {
    return new Promise((resolve, reject) => {
      axios
        .delete(config.API_URL + "/classroom/" + code, this.bearerConfig())
        .then((res) => {
          this.updateJWT(res);
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  newQuestion(classroom_id, question) {
    return new Promise((resolve, reject) => {
      axios
        .post(
          config.API_URL + "/question",
          {
            classroom_id: classroom_id,             
            title: question.title,
            description: question.description,
            phase: question.phase,
            pre_post: question.prepost,
            deadline: question.deadline,
          },
          this.bearerConfig()
        )
        .then((res) => {
          resolve(res.data);
        })
        .catch((err) => reject(err));
    });
  }

  delQuestion(id) {
    return new Promise((resolve, reject) => {
      axios
        .delete(config.API_URL + "/question/" + `${id}`, this.bearerConfig())
        .then((res) => {
          this.updateJWT(res);
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  // --------------------------------------------------------------------------
  // STUDENT API CALLS
  // --------------------------------------------------------------------------


  getStudentClassrooms() {
    return new Promise((resolve, reject) => {
      axios
        .get(config.API_URL + "/classrooms", this.bearerConfig())
        .then((res) => resolve(res.data.data))
        .catch((err) => reject(err));
    });
  }

  getStudentQuestions(code) {
    return new Promise((resolve, reject) => {
      axios
        .get(config.API_URL + "/questions/" + code, this.bearerConfig())
        .then((res) => resolve(res.data.data))
        .catch((err) => reject(err));
    });
  }

  subscribeToClassroom(code, email) {
    return new Promise((resolve, reject) => {
      axios
        .post(
          config.API_URL + "/enrolment/create", 
          {code: code, email: email},
          this.bearerConfig())
        .then((res) => resolve(res.data.data))
        .catch((err) => reject(err));
    });
  }

  answerQuestion(level, question) {
    // TODO API CALL
    return new Promise((resolve, reject) =>
      resolve({ message: _t("answer_recorded") })
    );
  }

  delUser(email, password) {
    return new Promise((resolve, reject) =>
      resolve({ message: _t("user_deleted") })
    );
  }

}

export default new BackendService();
